        .accordian__title{\n                padding-left: 4px;\n            }\n            svg {\n                    position: relative;\n                    left: -2px;\n                    height: 4px;\n                    width: 8px;\n                }\n        }\n        &:hover svg {\n            fill: $color-gray-42;\n        }\n\n    }\n    .accordian__container {\n        position: relative;\n        top: 0;\n        height: auto;\n        width: 100%;\n        // overflow: hidden;\n        transition: height .2s;\n    }\n    .accordian__title {\n         padding: 5px 10px 5px 8px;\n        cursor: pointer;\n        @include text-medium-semibold;\n        color: $color-gray-42;\n    }\n    /* override for mixin -- need to refactor this .. */\n    .dropdown-content {\n        width: 100%;\n        top: 45px;\n    }\n    .accordian__content {\n        padding: 8px 0px 16px 0px;\n        li {\n            cursor: pointer;\n            font-size: 12px;\n        }\n        input:focus + label {\n            opacity: 1!important;\n            transition: opacity .3s;\n        }\n    }\n    .input-time-range{\n        padding: 6px 0 20px 0;\n\n    }\n    \n}\n\n.remove-mask {\n    overflow: visible!important;\n}\n.white-bkgd {\n    background-color: white;\n}\n.panel-closed {\n    height: 0!important;\n}\n","/* shell for button view */\n.button-components {\n    .btn-add-bkgd-image {\n        max-width: 200px;\n    }\n}\n.btn{\n    cursor: pointer;\n    border: 0;\n    background: transparent;\n    >svg{\n        width: 16px;\n        height: 16px;\n    }\n    >a{\n        >svg{\n            width: 24px;\n            height: 24px;\n        }\n    }\n}\n/*rounded button*/\n.btn--rounded{\n    background: $color-gray-ee;\n    border: 0;\n    padding: 8px;\n    border-radius: 50%;\n    width: 42px;\n    height: 42px;\n    position: absolute;\n    margin-top: 8px;\n    svg{\n        color: $color-white;\n        fill: $color-white;\n    }\n}\n.btn--rounded--small{\n    @include rounded-border;\n    @include bg-green;\n    width: 13px;\n    height: 13px;\n    text-align: center;\n    position: relative;\n    svg{\n        height: 7px;\n        width: 7px;\n        fill: $color-white;\n        position: absolute;\n        top: 3px;\n        left: 3px;\n    }\n}\n.btn--green{\n    background: $color-green;\n}\n/* button add background image */\n.btn-batch-upload {\n    display: inline-block;\n    padding: 5px 15px;\n    border-radius: 25px;\n    border: solid 1px #e4e4e4;\n    font-weight: 500;\n    font-size: 12px;\n    cursor: pointer;\n    background-color: #eeeeee;\n}\n\n/* button add background image : file loader */\n.btn-add-bkgd-image {\n    @include flexCenter();\n    cursor: pointer;\n    color: $color-cobalt;\n    border: dashed 1px #e0e0e0;\n\n    .bkgd-image-input {\n        width: 0.1px;\n        height: 0.1px;\n        opacity: 0;\n        overflow: hidden;\n        position: absolute;\n        z-index: -1;\n    }\n\n    .bkgd-image-input + label {\n        cursor: pointer;\n        font-size: 16px;\n        font-weight: 400;\n        padding: 40px;\n    }\n}\n\n/* radio buttons */\n.radio-button-header {\n    font-weight: 600;\n    color: #424242;\n    font-size: 12px;\n}\n\n.radio-buttons-vertical {\n    display: flex;\n    flex-direction: column;\n\n}\n\n",".button-standard-gray {\n    @include text-medium-semibold;\n    height: 32px;\n    padding: 0 16px;\n    line-height: 1;\n    background-color: $color-gray-ee;\n    border: solid 1px $color-gray-e0;\n    border-radius: 25px;\n    &:hover {\n        cursor: pointer;\n        background-color: $color-gray-e0;\n    }\n    &.disabled,\n    &[disabled=\"disabled\"]{\n        cursor: default;\n        background-color: $color-gray-ee;\n        color: $color-gray-9e;\n    }\n}",".button-standard-blue {\n    @include text-medium;\n    height: 31px;\n    padding: 0 16px;\n    line-height: 1;\n    background-color: $color-purple;\n    border: solid 1px $color-purple;\n    border-radius: 25px;\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n    color: white;\n    &:hover {\n        cursor: pointer;\n        background-color: $color-cobalt-dark;\n        border: solid 1px $color-cobalt;\n    }\n}\n",".button-standard-green {\n    @include text-medium;\n    height: 32px;\n    padding: 0 16px;\n    line-height: 1;\n    background-color: $color-green;\n    border: solid 1px $color-green;\n    border-radius: 25px;\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n    color: white;\n    &:hover {\n        cursor: pointer;\n        background-color: $color-green-dark;\n        border: solid 1px $color-green;\n    }\n    &:disabled{\n        background-color: $color-gray-ee;\n        border: solid 1px $color-gray-e0;\n        color: $color-gray-9e;\n    }\n}\n","\n/**\n * Hide only visually, but have it available for screenreaders: h5bp.com/v\n * https://codepen.io/rgfx/pen/aixDn?editors=1100\n */\n\n.visuallyhidden {\n    border: 0;\n    clip: rect(0 0 0 0);\n    height: 1px;\n    margin: -1px;\n    overflow: hidden;\n    padding: 0;\n    position: absolute;\n    width: 1px;\n}\n\n/**\n *  Checkbox & Radio Button\n */\ninput[type=\"radio\"],\ninput[type=\"checkbox\"]{\n    @extend .visuallyhidden;\n}\n\ninput[type=\"radio\"] + label,\ninput[type=\"checkbox\"] + label{\n    @include text-medium;\n    position: relative;\n    padding-left: 25px;\n    color: $color-gray-42;\n    &:before{\n        position:absolute;\n        left:0;\n        content:\" \";\n        display:block;\n        width:10px;\n        height:10px;\n        border: solid 1px $color-gray-71;\n        background: $color-white;\n    }\n    &:hover:before {\n\n    }\n}\n\ninput[type='radio'] + label,\ninput[type='checkbox'] + label {\n    padding-left: 20px;\n    white-space: nowrap;\n    cursor: pointer;\n    line-height: 13px;\n}\n\ninput[type=\"radio\"]:focus + label,\ninput[type=\"checkbox\"]:focus + label{\n    text-decoration: none;\n}\n\n/**\n *  Checkbox\n */\n\ninput[type=\"checkbox\"]:checked + label {\n    &:before{\n        width:13px;\n        height:13px;\n        background: $color-purple;\n        border-radius: 50%;\n        border: solid 1px $color-gray-9e;\n        top: 2px;\n    }\n}\n\ninput[type='checkbox'] + label:after {\n    content: '';\n    display: block;\n    margin-left: 4px;\n    margin-top: -11px;\n    width: 5px;\n    height: 8px;\n    border: solid rgba(0,0,0,0);\n    border-width: 0 2px 2px 0;\n}\n\ninput[type='checkbox']:checked + label:after {\n    content: '';\n    display: block;\n    position: relative;\n    top: -3px;\n    right: 0px;\n    width: 5px;\n    height: 8px;\n    border: solid white;\n    border-width: 0 2px 2px 0;\n    transform: rotate(45deg);\n}\n\ninput[type='checkbox'] + label:before {\n    height: 13px;\n    width: 13px;\n    border-radius: 2px;\n}\n\ninput[type='checkbox']:checked + label:before {\n    height: 13px;\n    width: 13px;\n    background-color: $color-purple;\n    color: white;\n    border: solid 2px $color-purple;\n    border-radius: 2px;\n}\n\n/**\n *  Radio Button\n */\ninput[type=\"radio\"] + label:after {\n    left: 7px;\n    top: 7px;\n    height: 6px;\n    width: 6px;\n    border-radius: 50%;\n    background: #fff;\n}\n\ninput[type='radio'] + label:before {\n    border-radius: 50%;\n    height: 13px;\n    width: 13px;\n\n}\n\ninput[type='radio']:checked + label:before {\n    background: white;\n    border: solid 4px $color-purple;\n}\n\ninput[type='radio']:checked + label:after {\n    left: 7px;\n    top: 7px;\n    height: 6px;\n    width: 6px;\n    border-radius: 50%;\n    background: #fff;\n}\n\n",".form-elements {\n    margin: 40px;\n    width: 200px;\n\n    .radio-vertical {\n        display: flex;\n        flex-direction: column;\n    }\n    input[type=text] {\n        max-width: 300px;\n    }\n    li {\n        margin: 10px 0;\n    }\n\n    .text-input-example {\n        margin-botto