ant;\n    }\n    ::-webkit-input-placeholder {\n        color: #717171!important;\n        font-weight: 400;\n        font-family: 'Open Sans', sans-serif;\n        font-size: 12px;\n        letter-spacing: 0;\n    }\n    .btns-confirm {\n        position: relative;\n        top: -42px;\n        padding-right: 8px;\n        padding-bottom: 4px;\n        li {\n            font-size: 10px;\n        }\n    }\n}\n",".textarea-line-container {\n    @include text-area-line;\n    //height: 42px; // magic number\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    .textarea-line {\n        width: 100%;\n    }\n    label {\n        transition: opacity .2s;\n        vertical-align: top;\n        padding-bottom: 11px;\n    }\n    textarea,\n    div[contenteditable=\"true\"] {\n        outline: none;\n        border: none;\n        width: 100%;\n        resize: none;\n        padding: 0 0 4px 0;\n        border-bottom: solid 1px $color-gray-e0;\n        &:hover {\n            border-bottom: solid 1px $color-gray-71;\n        }\n    }\n\n    ::-webkit-input-placeholder {\n        // @include text-placeholder;\n        letter-spacing: 0;\n    }\n}\n",".dropdown {\n    @include dropdown;\n    .dropdown__button {\n        // add overrides here\n        width: 100%;\n    }\n    .dropdown__title {\n        // add overrides here\n    }\n\n    ul.dropdown__content {\n        @include dropdown__content(24px, 100%)\n    }\n}\n",".dropdown-icon {\n     @include dropdown-icon;\n    .dropdown-icon__button {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        position: relative;\n        svg {\n            height: 16px;\n            width: 16px;\n        }\n        .dropdown-arrow {\n            height: 10px;\n            width: 7px;\n            margin-left: 4px;\n        }\n    }\n    .dropdown-icon__title {\n        font-size: 12px;\n        padding-right: 10px;\n    }\n\n    ul.dropdown-icon__conten